// var app = getApp().globalData
export default {
	data() {
		return {
			list:[],
			pList:[],
			defaultList:[],
			lableString:''
			
		}
	},
	onLoad() {
	    this.getLable()
	},
	methods: {
		finish(){
			var s = ""
			if(this.pList.length>0){
				for(let val of this.pList){
					s += ','+val
				}
			    this.lableString = 	s.substr(1)
			}
			this.$api.add_label({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				label:this.lableString
			}).then(res=>{
				uni.redirectTo({
					url:"../../pagesTwo/ProjectList/ProjectList"
				})
				// uni.navigateBack({
				// 	delta:1
				// })
			})
		
		},
		// 获取自己添加的标签
		getAddLable(){
			this.$api.myself_label({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
			}).then(res=>{
				this.pList = res.data.data
				this.$nextTick(()=>{
					this.getTab()
				})
			})
		},	
		// 获取默认选项
		getLable(){
			this.$api.default_label({
			}).then(res => {
				this.defaultList = res.data.data
				this.$nextTick(()=>{
					this.getAddLable()
					// this.getTab()
				})
				
			})
		},
		getTab(){
			this.$api.Opportunity_label({
			}).then(res=>{
				this.list = res.data.data
				// console.log(this.list)
			})
		},
		selectTab(it,ind){
			if(ind>=this.defaultList.length){
				if(this.pList.indexOf(it)==-1){
					this.pList.push(it)
				}
				this.$nextTick(()=>{
					uni.pageScrollTo({
						scrollTop:1000
					})
				})
			}
			
			
			
			
			// if(this.status==1){
			// 	if(this.mList.indexOf(it)==-1){
			// 		this.mList.push(it)
			// 	}
			// }
			
			// if(this.status==2){
			// 	if(this.eList.indexOf(it)==-1){
			// 		this.eList.push(it)
			// 	}
			// }

		},
		delTab(it,ind){
			// if(ind>=this.defaultList.length){
				this.pList = this.pList.filter((item,index)=>item != it)
			// }
			// if(this.status==0){
			// 	this.pList = this.pList.filter((item,index)=>item != it)
			// }
			
			// if(this.status==1){
			// 	this.mList = this.mList.filter((item,index)=>item != it)
			// }
			
			// if(this.status==2){
			// 	this.eList = this.eList.filter((item,index)=>item != it)
			// }
			
		}
	}
}
